export const	REG_TAG	= /(?<name>[^\s;\]]+)/;	// testç”¨ã«export
export function	tagToken2Name_Args(token: string): [name: string, args: string] {
	const e = REG_TAG.exec(token.slice(1, -1));
	const g = e?.groups;
	if (! g) throw `ã‚¿ã‚°è¨˜è¿°ã€${token}ã€‘ç•°å¸¸ã§ã™(ã‚¿ã‚°è§£æ)`;

	const nm = g.name!;
	return [nm, token.slice(1 +nm.length, -1)];
}
export function	tagToken2Name(token: string): string {
	const e = REG_TAG.exec(token.slice(1));
	const g = e?.groups;
	if (! g) throw `ã‚¿ã‚°è¨˜è¿°ã€${token}ã€‘ç•°å¸¸ã§ã™(ã‚¿ã‚°è§£æ)`;

	return g.name!;
}

export function	splitAmpersand(token: string): {
		name: string;
		text: string;
		cast: string | undefined;
} {	// ãƒ†ã‚¹ãƒˆç”¨ã«public
	const equa = token.replaceAll('==', 'ï¼').replaceAll('!=', 'â‰ ').split('=');
		// != ã‚’å¼¾ã‘ãªã„ã®ã§ä¸­é€”åŠç«¯ã§ã¯ã‚ã‚‹
	const cnt_equa = equa.length;
	if (cnt_equa < 2 || cnt_equa > 3) throw 'ã€Œ&è¨ˆç®—ã€æ›¸å¼ã§ã¯ã€Œ=ã€æŒ‡å®šãŒä¸€ã¤ã‹äºŒã¤å¿…è¦ã§ã™';
	const [e0, e1, e2] = equa;
	if (e1!.startsWith('&')) throw 'ã€Œ&è¨ˆç®—ã€æ›¸å¼ã§ã¯ã€Œ&ã€æŒ‡å®šãŒä¸è¦ã§ã™';
	return {
		name: e0!.replaceAll('ï¼', '==').replaceAll('â‰ ', '!='),
		text: e1!.replaceAll('ï¼', '==').replaceAll('â‰ ', '!='),
		cast: (cnt_equa === 3 ?e2!.trim() :undefined)
	};
}


export class Grammar {
	constructor(private readonly cfg: IConfig) {this.setEscape('')}

	#REG_TOKEN	: RegExp;
	setEscape(ce: string) {
		if (this.#hC2M && (ce in this.#hC2M)) throw '[ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—æ–‡å­—] charã€'+ ce +'ã€‘ãŒç™»éŒ²æ¸ˆã¿ã®æ‹¬å¼§ãƒã‚¯ãƒ­ã¾ãŸã¯ä¸€æ–‡å­—ãƒã‚¯ãƒ­ã§ã™';

		// 1083 matches (14577 step, 9.9ms) https://regex101.com/r/dP0tAY/1
		/*
\\\S |
 \n+
| \t+
|	\[let_ml \s+ [^\]]+ ]
	.+? (?=\[endlet_ml [\]\s])
| \[ (?: [^"'#;\]]+
	| (["'#]).*?\1
	| ;[^\n]* ) *? ]
| ;[^\n]*
| &[^&\n]+&
| &&?(?: [^"'#;\n&]+ | (["'#]).*?\2 )+
| ^\*[^\s\[&;\\]+
| [^\n\t\[;\\]+
		*/
/*
	[^"'#;\]]++
	| (["'\#]).*?\1
			++ ã«ã—ãŸã„ã¨ã“ã‚ã ãŒã€jsã¯æœªã‚µãƒãƒ¼ãƒˆã‚‰ã—ã„ï¼ˆ2022/10/16ï¼‰
*/
		this.#REG_TOKEN = new RegExp(
		(ce	?`\\${ce}\\S|` :'')+	// ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—ã‚·ãƒ¼ã‚±ãƒ³ã‚¹
		'\\n+'+				// æ”¹è¡Œ
		'|\\t+'+			// ã‚¿ãƒ–æ–‡å­—
		`|\\[let_ml\\s+[^\\]]+\\]`+
			`.+?`+			// [let_ml]ã€œ[endlet_ml]é–“ã®ãƒ†ã‚­ã‚¹ãƒˆ
		`(?=\\[endlet_ml[\\]\\s])`+
		`|\\[(?:`+
			`[^"'#;\\]]+|`+	// ã‚¿ã‚°
			`(["'#]).*?\\1` +
				// . ã¯ (?:\\${ ce??'\\' }.|[^\\1]) ã§ãªãã¦ã‚ˆã•ã’
		`|;[^\\n]*)*?]`+
		'|;[^\\n]*'+		// ã‚³ãƒ¡ãƒ³ãƒˆ
		'|&[^&\\n]+&'+		// ï¼†è¡¨ç¤ºï¼†
		`|&&?(?:[^"'#;\\n&]+|(["'#]).*?\\2)+`+	// ï¼†ä»£å…¥
		'|^\\*[^\\s\\[&;\\\\]+'+	// ãƒ©ãƒ™ãƒ«
		`|[^\\n\\t\\[;${ce ?`\\${ce}` :''}]+`,		// æœ¬æ–‡
		'gs');
		this.#REG_CANTC2M = new RegExp(`[\\w\\s;[\\]*=&ï½œã€Šã€‹${ce ?`\\${ce}` :''}]`);
		this.#REG_TOKEN_NOTXT = new RegExp(`[\\n\\t;\\[*&${ce ?`\\${ce}` :''}]`);
	}


	// æ‹¬å¼§ãƒã‚¯ãƒ­ã®å®šç¾©
	bracket2macro(hArg: HArg, hTag: IHTag, scr: Script, start_idx: number) {
		const {name, text} = hArg;
		if (! name) throw '[bracket2macro] nameã¯å¿…é ˆã§ã™';
		if (! text) throw '[bracket2macro] textã¯å¿…é ˆã§ã™';
		const op = text.at(0);
		if (! op) throw '[bracket2macro] textã¯å¿…é ˆã§ã™';
		if (text.length !== 2) throw '[bracket2macro] textã¯æ‹¬å¼§ã®å‰å¾Œã‚’ç¤ºã™äºŒæ–‡å­—ã‚’æŒ‡å®šã—ã¦ãã ã•ã„';
		if (! (name in hTag)) throw `[bracket2macro] æœªå®šç¾©ã®ã‚¿ã‚°åˆã¯ãƒã‚¯ãƒ­[${name}]ã§ã™`;

		this.#hC2M ??= {};
		const cl = text.charAt(1);
		if (op in this.#hC2M) throw '[bracket2macro] textã€'+ op +'ã€‘ãŒç™»éŒ²æ¸ˆã¿ã®æ‹¬å¼§ãƒã‚¯ãƒ­ã¾ãŸã¯ä¸€æ–‡å­—ãƒã‚¯ãƒ­ã§ã™';
		if (cl in this.#hC2M) throw '[bracket2macro] textã€'+ cl +'ã€‘ãŒç™»éŒ²æ¸ˆã¿ã®æ‹¬å¼§ãƒã‚¯ãƒ­ã¾ãŸã¯ä¸€æ–‡å­—ãƒã‚¯ãƒ­ã§ã™';
		if (this.#REG_CANTC2M.test(op)) throw '[bracket2macro] textã€'+ op +'ã€‘ã¯æ‹¬å¼§ãƒã‚¯ãƒ­ã«ä½¿ç”¨ã§ããªã„æ–‡å­—ã§ã™';
		if (this.#REG_CANTC2M.test(cl)) throw '[bracket2macro] textã€'+ cl +'ã€‘ã¯æ‹¬å¼§ãƒã‚¯ãƒ­ã«ä½¿ç”¨ã§ããªã„æ–‡å­—ã§ã™';

		this.#hC2M[cl] = '0';	// ãƒã‚§ãƒƒã‚¯ç”¨ãƒ€ãƒŸãƒ¼
		this.#hC2M[op] = `[${name} text=`;

		this.addC2M(`\\${op}[^\\${cl}]*\\${cl}`, `\\${op}\\${cl}`);

		this.#replaceScr_C2M(scr, start_idx);
	}
	// ä¸€æ–‡å­—ãƒã‚¯ãƒ­ã®å®šç¾©
	char2macro(hArg: HArg, hTag: IHTag, scr: Script, start_idx: number) {
		const {char, name} = hArg;
		if (! char) throw '[char2macro] charã¯å¿…é ˆã§ã™';
		this.#hC2M ??= {};
		if (char in this.#hC2M) throw '[char2macro] charã€'+ char +'ã€‘ãŒç™»éŒ²æ¸ˆã¿ã®æ‹¬å¼§ãƒã‚¯ãƒ­ã¾ãŸã¯ä¸€æ–‡å­—ãƒã‚¯ãƒ­ã§ã™';
		if (this.#REG_CANTC2M.test(char)) throw '[char2macro] charã€'+ char +'ã€‘ã¯ä¸€æ–‡å­—ãƒã‚¯ãƒ­ã«ä½¿ç”¨ã§ããªã„æ–‡å­—ã§ã™';

		if (! name) throw '[char2macro] nameã¯å¿…é ˆã§ã™';
		if (! (name in hTag)) throw `[char2macro] æœªå®šç¾©ã®ã‚¿ã‚°åˆã¯ãƒã‚¯ãƒ­[${name}]ã§ã™`;

		this.#hC2M[char] = `[${name}]`;

		this.addC2M(`\\${char}`, `\\${char}`);

		this.#replaceScr_C2M(scr, start_idx);
	}
	#REG_CANTC2M	: RegExp;
	#REGC2M			= new RegExp('');
	#regStrC2M		= '';
	#regStrC2M4not	= '';
	addC2M(a: string, b: string) {
		this.#regStrC2M += `${a}|`;
		this.#regStrC2M4not += `${b}`;
		this.#REGC2M = new RegExp(
			`(${this.#regStrC2M}[^${this.#regStrC2M4not}]+)`, 'g');
	}


	resolveScript(txt: string): Script {
		const a = txt
		.replaceAll(/\r\n?/g, '\n')
		.match(this.#REG_TOKEN)
		?.flatMap(tkn=> {
			if (! this.testTagLetml(tkn)) return tkn;

			const r = /^([^\]]+?])(.*)$/s.exec(tkn);
			if (! r) return tkn;
			const [, a, b] = r;
			return [a, b];
		}) ?? [];

		const scr = {aToken: a as string[], len: a.length, aLNum: []};
		this.#replaceScr_C2M(scr);

		this.#replaceScript_Wildcard(scr);

		return scr;
	}


	readonly #REG_WILDCARD	= /^\[(call|loadplugin)\s/;
	readonly #REG_WILDCARD2	= /\bfn\s*=\s*[^\s\]]+/;
	#replaceScript_Wildcard(scr: Script) {
		for (let i=scr.len -1; i>=0; --i) {
			const token = scr.aToken[i]!;
			if (! this.#REG_WILDCARD.test(token)) continue;

			const [tag_name, args] = tagToken2Name_Args(token);
			this.#alzTagArg.parse(args);

			const p_fn = this.#alzTagArg.hPrm.fn;
			if (! p_fn) continue;
			const {val: fn} = p_fn;
			if (! fn || ! fn.endsWith('*')) continue;

			scr.aToken.splice(i, 1, '\t', '; '+ token);
			scr.aLNum.splice(i, 1, NaN, NaN);

			const ext = tag_name === 'loadplugin'
				? SEARCH_PATH_ARG_EXT.CSS
				: SEARCH_PATH_ARG_EXT.SN;
			const a = this.cfg.matchPath('^'+ fn.slice(0, -1) +'.*', ext);
			for (const v of a) {
				const nt = token.replace(
					this.#REG_WILDCARD2,
					'fn='+ decodeURIComponent(getFn(v[ext]!))
				);
				//console.log('\t='+ nt +'=');
				scr.aToken.splice(i, 0, nt);
				scr.aLNum.splice(i, 0, NaN);
			}
		}
		scr.len = scr.aToken.length;
	}
		readonly	#alzTagArg	= new AnalyzeTagArg;


	testTagLetml(tkn: string): boolean {return /^\[let_ml\s/.test(tkn)};
	testTagEndLetml(tkn: string): boolean {return /^\[endlet_ml\s*]/.test(tkn)};

	analyzToken(token: string): RegExpExecArray | null {	// 4LspWs
		this.#REG_TOKEN.lastIndex = 0;	// /gãªã®ã§å¿…è¦
		return this.#REG_TOKEN.exec(token);
	}


	#hC2M	: {[char: string]: string};
	#REG_TOKEN_NOTXT	: RegExp;
	#replaceScr_C2M(scr: Script, start_idx = 0): void {
		if (! this.#hC2M) return;

		for (let i=scr.len- 1; i >= start_idx; --i) {
			const token = scr.aToken[i]!;
			if (this.testNoTxt(token.at(0) ?? '\n')) continue;
				// çœç•¥æ™‚ã¯ #REG_TOKEN_NOTXT ã«å¼•ã£ã‹ã‹ã‚‹ \n ã«

			const lnum = scr.aLNum[i]!;
			const a = token.match(this.#REGC2M);
			if (! a) continue;
			let del = 1;
			for (let j=a.length -1; j>=0; --j) {
				let ch = a[j]!;
				const macro = this.#hC2M[ch.at(0) ?? ' '];
					// çœç•¥æ™‚ã¯ #REG_CANTC2M ã«å¼•ã£ã‹ã‹ã‚‹ ' ' ã«
				if (macro) {
					ch = macro +(macro.endsWith(']')
						? ''
						: (`'${ch.slice(1, -1)}']`));
					// æ–‡å­—åˆ—ã¯åŠè§’ç©ºç™½ã‚’æ„è­˜ã—ã¦''ã§å›²ã‚€ãŒã€ã„ãšã‚Œå¤‰ãˆãŸã„å ´åˆãŒã‚ã‚‹ï¼Ÿ
				}
				scr.aToken.splice(i, del, ch);

				scr.aLNum.splice(i, del, lnum);
				del = 0;
			}
		}
		scr.len = scr.aToken.length;
	}
	testNoTxt(ch: string): boolean {return this.#REG_TOKEN_NOTXT.test(ch)};	//4tst

}
